"use client";

import { Button } from "@/components/ui/button";
import { cn } from "@/lib/utils";

interface CalculatorButtonProps {
  value: string;
  onClick: (value: string) => void;
  className?: string;
  variant?: "default" | "operator" | "equals" | "function" | "number";
  span?: number;
}

export function CalculatorButton({
  value,
  onClick,
  className,
  variant = "default",
  span = 1,
}: CalculatorButtonProps) {
  const getButtonStyles = () => {
    const baseStyles = "h-16 text-lg font-medium rounded-2xl transition-all duration-200 active:scale-95";
    
    switch (variant) {
      case "operator":
        return cn(
          baseStyles,
          "bg-orange-500 hover:bg-orange-600 text-white border-0",
          className
        );
      case "equals":
        return cn(
          baseStyles,
          "bg-orange-500 hover:bg-orange-600 text-white border-0",
          className
        );
      case "function":
        return cn(
          baseStyles,
          "bg-gray-700 hover:bg-gray-600 text-white border-0",
          className
        );
      case "number":
        return cn(
          baseStyles,
          "bg-gray-800 hover:bg-gray-700 text-white border-0",
          className
        );
      default:
        return cn(
          baseStyles,
          "bg-gray-700 hover:bg-gray-600 text-white border-0",
          className
        );
    }
  };

  const getGridSpan = () => {
    if (span === 2) return "col-span-2";
    if (span === 3) return "col-span-3";
    return "col-span-1";
  };

  return (
    <Button
      onClick={() => onClick(value)}
      className={cn(getButtonStyles(), getGridSpan())}
      variant="ghost"
    >
      {value}
    </Button>
  );
}
