"use client";

import { useState } from "react";
import { Display } from "./Display";
import { ButtonGrid } from "./ButtonGrid";
import { CalculatorState, initialState, handleInput } from "@/lib/calculator";

export function Calculator() {
  const [state, setState] = useState<CalculatorState>(initialState);

  const onButtonClick = (value: string) => {
    setState((prevState) => handleInput(prevState, value));
  };

  return (
    <div className="w-full max-w-md mx-auto bg-black rounded-3xl shadow-2xl overflow-hidden">
      <div className="p-6">
        <Display value={state.display} />
      </div>
      <ButtonGrid onButtonClick={onButtonClick} isRadians={state.isRadians} />
    </div>
  );
}
