export type CalculatorState = {
  display: string;
  previousValue: number | null;
  operation: string | null;
  waitingForOperand: boolean;
  isRadians: boolean;
  memory: number;
};

export const initialState: CalculatorState = {
  display: "0",
  previousValue: null,
  operation: null,
  waitingForOperand: false,
  isRadians: true,
  memory: 0,
};

export const formatNumber = (num: number): string => {
  if (num === 0) return "0";
  if (Math.abs(num) < 0.000001 || Math.abs(num) > 999999999) {
    return num.toExponential(6);
  }
  return num.toString();
};

export const parseNumber = (str: string): number => {
  return parseFloat(str) || 0;
};

// Basic arithmetic operations
export const calculate = (firstOperand: number, secondOperand: number, operation: string): number => {
  switch (operation) {
    case "+":
      return firstOperand + secondOperand;
    case "-":
      return firstOperand - secondOperand;
    case "×":
      return firstOperand * secondOperand;
    case "÷":
      if (secondOperand === 0) throw new Error("Division by zero");
      return firstOperand / secondOperand;
    case "%":
      return firstOperand % secondOperand;
    case "x^y":
      return Math.pow(firstOperand, secondOperand);
    default:
      return secondOperand;
  }
};

// Scientific functions
export const scientificCalculate = (value: number, operation: string, isRadians: boolean = true): number => {
  const toRadians = (deg: number) => (deg * Math.PI) / 180;
  const toDegrees = (rad: number) => (rad * 180) / Math.PI;
  
  const angle = isRadians ? value : toRadians(value);
  
  switch (operation) {
    case "sin":
      return Math.sin(angle);
    case "cos":
      return Math.cos(angle);
    case "tan":
      return Math.tan(angle);
    case "ln":
      if (value <= 0) throw new Error("Invalid input for ln");
      return Math.log(value);
    case "lg":
      if (value <= 0) throw new Error("Invalid input for log");
      return Math.log10(value);
    case "√x":
      if (value < 0) throw new Error("Invalid input for square root");
      return Math.sqrt(value);
    case "x!":
      if (value < 0 || !Number.isInteger(value)) throw new Error("Invalid input for factorial");
      return factorial(value);
    case "1/x":
      if (value === 0) throw new Error("Division by zero");
      return 1 / value;
    case "x²":
      return value * value;
    case "e^x":
      return Math.exp(value);
    case "10^x":
      return Math.pow(10, value);
    default:
      return value;
  }
};

const factorial = (n: number): number => {
  if (n === 0 || n === 1) return 1;
  let result = 1;
  for (let i = 2; i <= n; i++) {
    result *= i;
  }
  return result;
};

// Constants
export const constants = {
  π: Math.PI,
  e: Math.E,
};

export const handleInput = (state: CalculatorState, input: string): CalculatorState => {
  const { display, previousValue, operation, waitingForOperand, isRadians } = state;

  try {
    // Numbers
    if (/^\d$/.test(input)) {
      if (waitingForOperand) {
        return {
          ...state,
          display: input,
          waitingForOperand: false,
        };
      }
      return {
        ...state,
        display: display === "0" ? input : display + input,
      };
    }

    // Double zero
    if (input === "00") {
      if (waitingForOperand) {
        return {
          ...state,
          display: "0",
          waitingForOperand: false,
        };
      }
      return {
        ...state,
        display: display === "0" ? "0" : display + "00",
      };
    }

    // Decimal point
    if (input === ".") {
      if (waitingForOperand) {
        return {
          ...state,
          display: "0.",
          waitingForOperand: false,
        };
      }
      if (display.indexOf(".") === -1) {
        return {
          ...state,
          display: display + ".",
        };
      }
      return state;
    }

    // Clear all
    if (input === "AC") {
      return initialState;
    }

    // Backspace
    if (input === "⌫") {
      if (display.length > 1) {
        return {
          ...state,
          display: display.slice(0, -1),
        };
      }
      return {
        ...state,
        display: "0",
      };
    }

    // Constants
    if (input === "π") {
      return {
        ...state,
        display: formatNumber(constants.π),
        waitingForOperand: false,
      };
    }

    if (input === "e") {
      return {
        ...state,
        display: formatNumber(constants.e),
        waitingForOperand: false,
      };
    }

    // Toggle radians/degrees
    if (input === "rad") {
      return {
        ...state,
        isRadians: !isRadians,
      };
    }

    // Scientific functions (unary operations)
    if (["sin", "cos", "tan", "ln", "lg", "√x", "x!", "1/x", "x²", "e^x", "10^x"].includes(input)) {
      const currentValue = parseNumber(display);
      const result = scientificCalculate(currentValue, input, isRadians);
      return {
        ...state,
        display: formatNumber(result),
        waitingForOperand: true,
      };
    }

    // Binary operations
    if (["+", "-", "×", "÷", "%", "x^y"].includes(input)) {
      const inputValue = parseNumber(display);

      if (previousValue === null) {
        return {
          ...state,
          previousValue: inputValue,
          operation: input,
          waitingForOperand: true,
        };
      }

      if (operation && !waitingForOperand) {
        const result = calculate(previousValue, inputValue, operation);
        return {
          ...state,
          display: formatNumber(result),
          previousValue: result,
          operation: input,
          waitingForOperand: true,
        };
      }

      return {
        ...state,
        previousValue: inputValue,
        operation: input,
        waitingForOperand: true,
      };
    }

    // Equals
    if (input === "=") {
      const inputValue = parseNumber(display);

      if (previousValue !== null && operation) {
        const result = calculate(previousValue, inputValue, operation);
        return {
          ...state,
          display: formatNumber(result),
          previousValue: null,
          operation: null,
          waitingForOperand: true,
        };
      }
      return state;
    }

    // Parentheses (for future implementation)
    if (input === "(" || input === ")") {
      // Basic implementation - can be expanded for full expression parsing
      return state;
    }

    return state;
  } catch (error) {
    return {
      ...state,
      display: "Error",
      previousValue: null,
      operation: null,
      waitingForOperand: true,
    };
  }
};
